.stats {
	background-image: url('../assets/stats wave.png');
	background-repeat: no-repeat;
	background-size: cover;
	height: 100%;
	padding: 70px 0;
}

.stats h2 {
	margin: 40px 0;
	font-size: 40px;
	font-weight: 500;
	text-align: center;
	color: #0d0d0d;
}

.details {
	margin: 50px 100px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.details div {
	margin-right: 20px;
	display: flex;
	justify-content: flex-start;
}

.details div .left {
	display: block;
}

.left h5 {
	font-size: 50px;
	color: #2a375a;
}

.left h6 {
	font-size: 13px;
	color: #3f4f7c;
}

.stats button {
	display: block;
	margin: 0 auto;
}

@media only screen and (max-width: 400px) {
	.stats div {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.details {
		margin: 20px;
	}

	.details div {
		margin: 0;
		padding: 5px;
	}
}
