.footer {
	background-color: #ededed;
	padding: 20px 0;
}

.footer img {
	width: 80px;
	height: 80px;
}

.footer img:hover {
	scale: 1.1;
}

.top {
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.top a {
	text-decoration: none;
	color: #0d0d0d;
	font-weight: 600;
}

.top a:hover {
	color: #5abf90;
}

hr.line {
	display: block;
	margin: 20px auto;
	border-color: #ededed82;
	width: 80%;
}

.bottom {
	display: flex;
	justify-content: space-around;
	align-items: center;
	margin-bottom: 20px;
}

.bottom div a {
	text-decoration: none;
	color: #0d0d0d;
	margin-right: 20px;
}

.bottom div a:hover {
	color: #3f4f7c;
}

.bottom div img {
	width: 30px;
	height: 30px;
	margin-left: 20px;
	cursor: pointer;
	border-radius: 50px;
}

.bottom div img:hover {
	scale: 1.2;
}

@media only screen and (max-width: 400px) {
	.top {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.top a {
		margin: 5px 0;
	}

	.bottom {
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
	}

	.bottom div img {
		margin: 15px;
	}
}
