.slider {
	margin: 20px 0;
}

.test {
	text-align: center;
}

.test img {
	width: 100px;
	height: auto;
	border-radius: 70px;
	border: 2px solid #edededaa;
}

.test p {
	font-size: 20px;
	margin-bottom: 20px;
	color: #edededaa;
}

.test h4 {
	text-transform: uppercase;
}

.buttons {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 20px 0;
}

.buttons button {
	margin: 0 10px;
}
