.container {
	background-color: #ededed;
	height: 550px;
	margin: 40px;
	border-radius: 15px;
	box-shadow: 5px 5px 25px #0d0d0da8;
}

.form {
	padding: 40px;
}

.form h3 {
	font-weight: 400;
	margin-bottom: 16px;
}

.inputs {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.inputs input {
	width: 100%;
	margin-bottom: 10px;
	padding: 10px;
	border-radius: 5px;
	outline: #cbcaca;
	border: 1px solid #cbcaca;
}

.inputs input::placeholder {
	color: #a4a2a2;
}

.form p {
	margin: 15px 0;
	color: #0d0d0d;
}

.form input {
	margin: 5px;
}

.form label {
	margin: 10px;
	color: #0d0d0d;
}

.form button {
	display: block;
	margin: 10px auto;
}

@media only screen and (max-width: 400px) {
	.container {
		margin: 10px;
		min-height: 100vh;
	}

	.form {
		padding: 20px;
	}
}
