.banner {
	height: 100vh;
	padding: 20px 100px;
	background-image: url('../assets/banner background.png');
	background-repeat: no-repeat;
	background-size: cover;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
}

.text {
	margin: 40px;
}

.text h1 {
	font-size: 55px;
	color: #ededed;
	font-weight: 600;
}

.text h2 {
	margin-top: 15px;
	color: #ededed;
	font-weight: 300;
}

@media only screen and (max-width: 400px) {
	.banner {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 40px;
	}

	.text {
		margin: 20px;
	}

	.text h1 {
		font-size: 40px;
	}
}
