.container {
	margin: 10px 100px 70px;
}

.container h2 {
	text-align: center;
	font-size: 40px;
	font-weight: 400;
	margin-bottom: 50px;
}

.services {
	display: flex;
	justify-content: center;
	align-items: center;
}

.services div img {
	display: block;
	margin: 0 auto;
	width: 100px;
	height: 100px;
	background-color: #e6e5e5;
	border-radius: 40px;
	border: none;
	padding: 16px;
}

.services div h3 {
	margin: 20px 0;
	text-align: center;
}

.services div p {
	font-weight: 300;
	margin: 20px;
	padding: 0 10px;
	text-align: center;
}

@media only screen and (max-width: 400px) {
	.container {
		margin-top: 450px;
	}
	.services {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}
