.header {
	padding: 16px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header img {
	width: 50px;
	height: 50px;
}

.header img:hover {
	scale: 1.1;
}

.header nav a {
	margin: 0 16px;
	text-decoration: none;
	color: #0d0d0d;
	font-size: 20px;
	font-weight: 500;
}

.header nav a:hover {
	color: #0d0d0d7e;
	text-decoration: underline #5abf90 3px;
}

button {
	padding: 10px 25px;
	background-color: #ededed;
	color: #0d0d0d;
	border: 1px solid #5abf90;
	border-radius: 25px;
	cursor: pointer;
	text-transform: uppercase;
}

button:hover {
	color: #ededed;
	background-color: #5abf90;
	border: 1px solid #5abf90;
}

.menu {
	display: none;
}

@media only screen and (max-width: 400px) {
	.header {
		display: none;
	}

	.menu {
		width: 100%;
		padding: 10px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		background-color: #dad7d7;
	}

	.open {
		margin: 10px 0;
		color: #3f4f7c;
		text-decoration: none;
	}

	.closed {
		display: none;
	}
}
